import React, { Component } from 'react';
import Countdown from './Countdown.js';
import './CountDownApp.css';

var countDownSectionStyle = {
    // backgroundImage: "url(" + require('./images/bg-countdown.jpg') + ")",
    // backgroundSize: "cover"
};

class CountDownApp extends Component {
  render() {
    const currentDate = new Date();
    const year = (currentDate.getMonth() === 11 && currentDate.getDate() > 23) ? currentDate.getFullYear() + 1 : currentDate.getFullYear();
    return (
      <div className="CountDownApp overlay1" style={countDownSectionStyle}>
        <div className="CountDownApp-header ">
            <h3 className="title">Competition is coming soon (Midnight of 23rd to 24th Dec, UTC time):</h3>
            <Countdown date={`${year}-12-24T00:00:00`} />
        </div>


      </div>
    );
  }
}

export default CountDownApp;
